import * as actionTypes from "./actionTypes";

export function getReposSuccess(repos) {
  return {
    type: actionTypes.GET_REPOS_SUCCESS,
    payload: repos,
  };
}

export function getLangSuccess(langs) {
  return {
    type: actionTypes.GET_LANGUAGE_SUCCESS,
    payload: langs,
  };
}

export function getRepos(username) {
  return async function (dispatch) {
    let url = `https://api.github.com/users/${username}/repos`;
    const res = await fetch(url);
    const res_1 = await res.json();
    return dispatch(getReposSuccess(res_1));
  };
}

export function getLangs(repos) {
  return async function (dispatch) {
    let langsList = [];
    for (const repo of repos) {
      fetch(repo.languages_url)
        .then((res) => res.json())
        .then((res) => langsList.push({ id: repo.id, langs: res }));
    }
    return dispatch(getLangSuccess(langsList));
  };
}
