import React, { Component } from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";

import "./GithubCard.css";
import * as githubActions from "../../redux/actions/githubActions";

class GithubCard extends Component {
  componentDidMount() {
    this.getProps();
  }

  async getProps() {
    await this.props.actions.getRepos("ahmettlevent");
    if (this.props.gitRepos.length > 0)
      this.props.actions.getLangs(this.props.gitRepos);
  }

  render() {
    return (
      <div id="github" className="GitHub-Container">
        <span className="Github-nm">Github Repos</span>

        <div className="GithubRepos-Container">
          <ul className="GithubReposList">
            {this.props.gitRepos.map((repo) => (
              <li key={repo.id} className="GithubRepoListItem">
                <div className="GithubRepo">
                  {/* <div className="repoImage">
                    <img alt="owner" src={repo.owner.avatar_url}></img>
                  </div> */}

                  <div className="ct-des-name">
                    <div className="repoName">
                      <a href={repo.html_url}>
                        {repo.name}
                        &nbsp; &nbsp;
                        <i className="fas fa-external-link-alt"></i>
                      </a>
                    </div>
                    <div className="repoDescription">
                      <p>
                        {repo.description != null
                          ? repo.description
                          : repo.name === "ahmettlevent"
                          ? "Profil Reposu"
                          : "Bu repoda Açıklama Bulunmamaktadır"}
                      </p>
                    </div>
                  </div>
                </div>
              </li>
            ))}
          </ul>
        </div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    gitRepos: state.reposReducer,
    gitRepoLanguages: state.repoLangsReducer,
  };
}
function mapDispatchToProps(dispatch) {
  return {
    actions: {
      getRepos: bindActionCreators(githubActions.getRepos, dispatch),
      getLangs: bindActionCreators(githubActions.getLangs, dispatch),
    },
  };
}
export default connect(mapStateToProps, mapDispatchToProps)(GithubCard);
