import React, { Component } from "react";
import "./UserCard.css";
import pp from "../../static/img/pp.jpg";

export default class UserCard extends Component {
  render() {
    const navImage = {
      backgroundImage: `url(${pp})`,
    };
    return (
      <div id="profile" className="ProfileCart">
        <div className="navImage" style={navImage}></div>
        <h1 className="navName">Ahmet Utku Levent</h1>
        <ul className="navList">
          <li className="navList-item">
            <a href="/">Github</a>
          </li>
          <li className="navList-item">
            <a href="#about">About</a>
          </li>
          <li className="navList-item">
            <a href="/">LinkedIn</a>
          </li>
        </ul>
      </div>
    );
  }
}
